			position: absolute;
			z-index: 2;
		}
		.sa-line.sa-tip {
			width: 12px;
			left: 18px;
			top: 34px;
			-webkit-transform: rotate(45deg);
			transform: rotate(45deg);
			-ms-transform: rotate(45deg) \9;
		}
		.sa-line.sa-long {
			width: 25px;
			right: 13px;
			top: 29px;
			-webkit-transform: rotate(-45deg);
			transform: rotate(-45deg);
			-ms-transform: rotate(-45deg) \9;
		}
	}
	.sa-icon.sa-custom {
		background-size: contain;
		border-radius: 0;
